.link-input-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
  width: 100%;
  color: white;

  input {
    border: 0;
    outline: 0;
    height: 25px;
    font-size: 12px;
    width: 230px;
    padding-inline-start: 0.5rem;
    background-color: #3c3c3c;
    color: white;
    max-width: 100%;
    width: 100%;
    &::-webkit-input-placeholder {
      /* Edge */
      color: rgba(255, 255, 255, 0.775);
    }

    &:-ms-input-placeholder {
      /* Internet Explorer 10-11 */
      color: rgba(255, 255, 255, 0.775);
    }

    &::placeholder {
      color: rgba(255, 255, 255, 0.775);
    }
  }
}
