header {
  // position: fixed;
  width: 100%;
  z-index: 200;
  transition: all 0.5s;
}
.app-header {
  z-index: 15;
  padding: 0 3.6rem;
  height: 70px;
  // background-color: $accent-color;
  color: $main-color;
  background-color: #ffffffd6;
  // box-shadow: 0 1px 5px 0 rgb(41 85 115 / 21%);

  &.in-editor {
    padding: 0 4.6rem;
  }
  &.in-dashboard {
    padding: 0 2rem;
  }
  &.header-nav-menu-open {
    background-color: rgb(61, 73, 110);
    // color: rgb(203 231 242);
    color: rgb(255, 176, 149);
    .logo {
      // color: rgb(203 231 242);
      color: rgb(255, 176, 149);
    }
  }

  .logo {
    color: $main-color;
    height: 100%;
    display: flex;
    font-family: $logo-font;
    font-size: 2.7rem;
    line-height: 65px;
  }
  .welcome-msg {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;

    p {
      font-weight: 700;
      text-transform: capitalize;
    }

    .logout-icon {
      display: flex;
      gap: 7px;
      border-radius: 16px;
      padding: 0.4rem 0.5rem;

      .logout-svg {
        font-size: 1.7rem;
      }
      &:hover {
        background-color: rgb(221 221 221 / 36%);
      }
    }
  }

  .navbar {
    font-size: 1.2rem;
    display: none;
  }

  .menu-icon {
    display: block;
    cursor: pointer;
    font-size: 2rem;
  }

  .main-menu-container {
    position: fixed;
    background-color: rgb(61, 73, 110);
    width: 100%;
    transition: all 0.25s ease-in-out 0s;
    top: 70px;
    left: 0px;
    opacity: 1;
    visibility: visible;
    min-height: 100vh;
    z-index: 1000;

    & > a {
      border-bottom: 1px solid rgb(41, 24, 47);
      padding-left: 60px;
      width: 100%;
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      color: rgb(203, 231, 242);
      height: 90px;
      cursor: pointer;
      font-weight: bold;
      font-size: 1.5rem;
    }

    a:hover {
      text-decoration: none;
      background-color: rgb(41, 24, 47);
      color: rgb(203, 231, 242);
    }
  }

  @media screen and (max-width: '450px') {
    & {
      padding: 0 0.5rem;
    }
  }

  @media screen and (min-width: '1200px') {
    &.header-nav-menu-open {
      background-color: unset;
      color: $main-color;
      .logo {
        // color: rgb(203 231 242);
        color: $main-color;
      }
    }
    & {
      padding: 0 280px;
      .navbar {
        display: flex;
        align-items: center;
      }
    }
    .main-menu-container {
      display: none;
    }
    .menu-icon {
      display: none;
    }
  }

  .publish-btn {
    background-color: rgb(229, 97, 53);
    color: white;
    border-radius: 16px;
    display: flex;
    align-items: center;
    padding: 5px 26px;
    gap: 6px;
    font-size: 1.2rem;
    /* box-shadow: 0 0 0px 0px #00000073; */
    border: 1px solid #00000000;
    cursor: pointer;
  }
  @media screen and (min-width: '1300px') {
    .navbar {
      font-size: 1.2rem;
    }
  }
}
